import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

const Header = styled.h1`
  color: ${(props) => props.color} !important;
  text-align: center;
`;

const Subtitle = styled.div`
  color: ${(props) => props.color};
  text-align: center;
  margin-bottom: 15px;
`;

const SeactionHeader = (props) => {
  return (
    <div>
      <Header color={props.headerColor}>{props.title}</Header>
      <Subtitle color={props.subtitleColor}>{props.subtitle}</Subtitle>
    </div>
  );
};

SeactionHeader.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
};

SeactionHeader.defaultProps = {
  headerColor: "#ffffff",
  subtitleColor: "#ffffff",
};

export default SeactionHeader;
