import React, { Component } from "react";
import SeactionHeader from "./common/section-header";
import styled, { ThemeProvider } from "styled-components";
import { devices } from "../devices";
import PriceBadge from "./price-badge";
import { Card, CardTitle, ImagePlaceholder } from "./car-card";
import Details from "./details";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import { beUrl } from "../config";

const Container = styled.div`
  background-color: ${(props) => props.bg};
  padding: 2% 5%;
`;

const CenteredContent = styled.div`
  width: 80%;
  position: relative;
  margin: 0 auto;
  top: calc(50% - 20px);
  text-align: center;

  & a {
    text-decoration: none;
    color: ${(props) => props.theme.card.primaryText};
    transition: 0.3s;

    ${Card}:hover & {
      color: ${(props) => props.theme.card.primaryTextHover};
    }
  }

  @media ${devices.laptop} {
    width: 95%;
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 15px;
  grid-auto-rows: minmax(400px, auto);

  @media ${devices.laptopL} {
    max-width: 1000px;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-row-gap: 25px;
  }

  @media ${devices.tablet} {
    max-width: 400px;
    margin: 0 auto;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 2fr) 1fr;
    grid-row-gap: 25px;
  }
`;

class Showroom extends Component {
  state = {
    isLoading: true,
  };

  async componentDidMount() {
    const { data: cars } = await axios.get(`${beUrl}/cars`);

    this.setState({ cars });
  }

  handleLoad = () => {
    this.setState({ isLoading: false });
  };

  render() {
    const { history } = this.props;
    const { cars, isLoading } = this.state;

    return (
      <Container bg={this.props.theme.bg}>
        <SeactionHeader
          title={"As nossas Viaturas"}
          subtitle={"Encontre aqui um bom negócio para o seu próximo carro."}
          headerColor={this.props.theme.primary}
          subtitleColor={this.props.theme.secundary}
        />

        {!cars ? (
          <LoadingContainer>
            <LoadingSpinner></LoadingSpinner>
          </LoadingContainer>
        ) : (
          <Grid>
            {cars
              .filter((car) => car.showroom)
              .map((car) => {
                const mileage = car.attributes.kms;
                const year = car.attributes.year.value;
                const fuelType = car.attributes.fuelType.value;

                return (
                  <ThemeProvider theme={this.props.theme.card} key={car.id}>
                    <Card onClick={() => history.push(`/carro/${car.id}`)}>
                      <div>
                        {isLoading && <ImagePlaceholder></ImagePlaceholder>}
                        <img
                          src={
                            process.env.PUBLIC_URL +
                            `/img/${car.id}/thumbnail.webp`
                          }
                          alt={"Foto principal da viatura"}
                          onLoad={this.handleLoad}
                        />
                      </div>
                      <CardTitle>{car.title}</CardTitle>

                      <Details
                        year={year}
                        mileage={mileage}
                        fuelType={fuelType}
                      />

                      <PriceBadge
                        badge={this.props.theme.badge}
                        price={car.price}
                      />
                    </Card>
                  </ThemeProvider>
                );
              })}
            <ThemeProvider theme={this.props.theme}>
              <Card onClick={() => history.push("/carros")}>
                <CenteredContent>
                  <a href="/carros">
                    <h4>
                      Veja todas as nossas ofertas.{" "}
                      <i className="fas fa-arrow-right"></i>
                    </h4>
                  </a>
                </CenteredContent>
              </Card>
            </ThemeProvider>
          </Grid>
        )}
      </Container>
    );
  }
}

export default Showroom;
